import React from "react";
import "./Product.css";
import { useStateValue } from "./StateProvider";
import Valentine from "./Valentine.jpg";

function Product({ id, title, price, image, Link, Link1 }) {
  const [{ basket }, dispatch] = useStateValue();
  
  const addToBasket = () => {
    dispatch({
      type: "ADD_TO_BASKET",
      item: {
        id: id,
        title: title,
        image: image,
        price: price,
        // rating:rating,
      },
    });
  };
  return (
    <div className="product">
      <div className="product__info">
        {/* <p className="product__id">{id}</p> */}
        <p className="product__title">
          {title}
          <a href="" className="paragraphLink1">
            {Link1}
          </a>
        </p>
        <p className="product__price">
          <small>$</small>
          <strong>{price}</strong>
        </p>
      </div>
      <img src={image} alt="" />
      <a href="" className="paragraphLink">
        {Link}
      </a>
      <button onClick={addToBasket}>Add to Basket</button>
    </div>
  );
}

export default Product;
